@import url('https://fonts.googleapis.com/css2?family=Ubuntu&display=swap');
*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  background-color: darksalmon;
  font-family: 'Ubuntu', sans-serif;
}

h1{
  text-align: center;
  background-color: blanchedalmond;
}

.book{
  border: 1px solid white;
  background-color: white;
  width: 16rem;
  padding: 3px;
}

.book:hover{
  box-shadow: 2px 2px 15px 5px dimgrey;
  transition: 0.4s;
}

.book h4{
 font-size: 1rem;
 margin-top: 0.2rem;
}
#desc{
  color: black;
  font-size: 15px;
  text-align: justify;
  padding: 3px;
}

.book p{
  color: red;
  font-size: 0.8rem;
}

.book span{
  margin-top: 1rem;
}

.bookID{
  display: flex;
  justify-content: center;

}

img{
  border-radius: 8px;
  opacity: 0.7;
  box-shadow: none;
  width: 15.5rem;
  height: 19rem;
}

.grid{
  width: 80vw;
  max-width: 1170px;
  margin: 5rem auto;
  display: grid;
  grid-row-gap: 40px;
  align-items: start;
}

.center h1{
  text-align: center;
  letter-spacing: 10px;
  border-radius: 8px;
  border: 1px solid white;
  background-color: white;
}



@media only screen and (max-width: 634px) {
  .grid{
    grid-template-columns: repeat(1,1fr);
  }
}

@media only screen and (min-width: 634px) {
  .grid{
    grid-template-columns: repeat(2,1fr);
  }
}

@media only screen and (min-width: 950px) {
  .grid{
    grid-template-columns: repeat(3,1fr);
  }
} 





